.icon-left {
  position: absolute;
  top: 10px;
  left: 10px;
}
.icon-right {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.season-display {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.winter {
  background-color: aliceblue;
}

.summer {
  background-color: orange;
}

.season-display.winter {
  color: #81d4fa;
}

.season-display.summer {
  color: #fff176;
}

.season-display.winter.sentence {
  color: white;
}

.error-message {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
  font-size: 18px;
  font-weight: bold;
}
